img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.container {
  max-width: 1440px;
  padding: 0;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 768px; } }
  @media (max-width: 767px) {
    .container {
      width: 320px; } }

.page {
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #000000;
  margin: 0;
  padding: 0; }

.page-body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  color: transparent; }

.main-nav__wrapper {
  text-align: center; }

.page-header__logo {
  position: absolute;
  top: 312px;
  left: calc(50% - 440px); }

.main-nav {
  position: fixed;
  width: 873px;
  min-height: 62px;
  left: calc(50% - 436px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.main-nav__toggle {
  display: none; }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end; }
  @media (max-width: 1023px) {
    .main-nav__list {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column; } }

.site-list__item {
  padding-top: 17px; }

.site-list__link {
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  letter-spacing: -0.005em;
  text-decoration: none;
  color: #ffffff;
  opacity: 0.7; }

.slogan {
  min-height: 825px;
  background-color: #131314; }
